@import 'variables';
@import 'mixins';

/* ==========================================================================
   #Comments styles - start
   ========================================================================== */

.mkdf-comment-holder {
	@include mkdfRelativeHolderLayout();
	padding: 0;
	box-sizing: border-box;
	
	.mkdf-comment-holder-inner {
		padding: 70px 0 20px;
	}
	
	.mkdf-comments-title {
		margin-bottom: 33px;
	}
	
	.mkdf-comment {
		display: flex;
		align-items: flex-start;
		border-bottom: 1px solid #424242;
		padding-bottom: 35px;

		@include phone-portrait {
			display: block;
		}
		
		&.mkdf-pingback-comment {
			
			.mkdf-comment-text {
				padding: 0;
			}
		}
	}
	
	.mkdf-comment-list {
		position: relative;
		padding: 0;
		margin: 0;
		list-style: none;
		z-index: 150;
		
		li {
			@include mkdfRelativeHolderLayout();
			margin: 25px 0 0;
			padding: 6px 0 0;
		}
		
		> li:first-child {
			margin: 0;
			padding: 0;
		}
		
		.children {
			padding: 0 0 0 110px;
		}
	}
	
	.mkdf-comment-image {
		position: relative;
		display: block;
		flex-shrink: 0;
		left: 0;
		top: 10px;
		width: 74px;
		height: 74px;
		padding-right: 34px;
		
		img {
			display: block;
		}
	}
	
	.mkdf-comment-text {
		padding: 0;
		width: 100%;
		min-height: 82px;
		
		.mkdf-comment-info {
			@include mkdfRelativeHolderLayout();
			display: flex;
			justify-content: space-between;
			margin: 6px 0 2px;
			
			.mkdf-comment-info-link {
				flex-shrink: 0;
			}
			
		}
		
		.replay,
		.comment-reply-link,
		.comment-edit-link {
			position: relative;
			display: inline-block;
			vertical-align: middle;
			float: right;
			width: auto;
			margin: 0 0 0 10px;
			padding: 0 !important;
			font-family: $additional-button-font;
			font-size: 12px;
			line-height: 2em;
			font-weight: 700;
			letter-spacing: 0.250em;
			text-transform: uppercase;
			color: $default-heading-color;
			background-color: transparent;
			border: 0;
			outline: none;
			box-sizing: border-box;
			transition: color .2s ease-in-out, background-color .2s ease-in-out, border-color .2s ease-in-out;

			&:before {
				content: '';
	            position: absolute;
	            right: 0;
	            bottom: 2px;
	            width: calc(100% + 4px);
	            height: 10px;
	            z-index: 1;
	            background-image: url('../img/simple_button_pattern.png');
            	background-repeat: repeat;
            	opacity: .7;
            	transition: .5s;
	        }

	        &:after {
		        content: '';
	            position: relative;
	            z-index: 2;
	            display: inline-block;
	            vertical-align: middle;
	        }
			
			&:hover {

				&:before {
	                background-position: 20px 0;
	            }
			}
		}
		
		.mkdf-comment-name {
			float: left;
			margin: 0;
			padding-right: 12px;
			letter-spacing: 0;
		}
		
		.mkdf-comment-date {
			position: relative;
			bottom: 3px;
			float: left;
			margin: 0;
			@include mkdfHeadingStyle(h6);
		}
		
		.mkdf-text-holder p {
			margin: 7px 0;
		}
		
		.comment-respond {
			margin: 20px 0;
			
			.comment-reply-title {
				margin: 0 0 10px;
				font-size: 14px;
			}
			
			.logged-in-as {
				margin: 0 0 8px;
			}
		}
		
		#cancel-comment-reply-link {
			margin-left: 10px;
			font-size: 14px;
			color: $first-main-color;
			
			&:hover {
				color: $default-heading-color;
			}
		}
		
		ul, ol {
			
			li {
				display: list-item;
			}
		}
		
		li {
			margin: 0 !important;
			padding: 0 !important;
			border: 0 !important;
		}
	}
}

.mkdf-comment-form {
	@include mkdfRelativeHolderLayout();
	margin: 79px 0 40px;
	clear: both;
	
	.comment-reply-title {
		margin: 0 0 39px;
	}
	
	.logged-in-as {
		margin: 0 0 8px;
	}
	
	.comment-form {
		
		.form-submit {
			
			.mkdf-btn {
				
				&:after {
					content: '';
					position: absolute;
					right: 0;
					bottom: 2px;
					width: calc(100% + 4px);
					height: 10px;
					z-index: -1;
					background-image: url('../img/simple_button_pattern.png');
            		background-repeat: repeat;
            		opacity: .7;
            		transition: .5s;
				}
				
				&:hover:after {
					background-position: 20px 0;
				}
			}
		}
	}
}

.comment-form-cookies-consent {
	margin: 0 0 15px;

	input {
		position: relative;
		top: 1px;
		margin: 0 10px 0 0;
	}
}

@include ipad-portrait {
	
	.mkdf-comment-holder {
		
		.mkdf-comment-list {
			
			.children {
				padding: 0 0 0 40px;
			}
		}
	}
}

@include phone-landscape {
	
	.mkdf-comment-holder {
		
		.mkdf-comment-list {
			
			.children {
				padding: 0;
			}
		}
		
		.mkdf-comment-text {
			
			.mkdf-comment-date,
			.mkdf-comment-name {
				float: none;
			}
			
			.replay,
			.comment-reply-link,
			.comment-edit-link {
				float: none;
				margin: 0 10px 0 0;
			}
		}
	}
}

@include phone-portrait {
	
	.mkdf-comment-holder {
		
		.mkdf-comment-image {
			float: none;
		}
		
		.mkdf-comment-text {
			padding: 20px 0 0;
			min-height: inherit;
		}
	}
}

/* ==========================================================================
   #Comments styles - end
   ========================================================================== */

#respond textarea,
.mkdf-style-form textarea,
#respond input[type='text'],
input[type='text'],
input[type='email'],
input[type='password'],
.post-password-form input[type='password'] {
	@extend %input-style;
}

.post-password-form input[type='password'] {
	margin-top: 10px;
}

.wpcf7-form-control {
	&.wpcf7-text,
	&.wpcf7-number,
	&.wpcf7-date,
	&.wpcf7-textarea,
	&.wpcf7-select,
	&.wpcf7-quiz {
		@extend %input-style;
		
		footer & {
			margin: 0;
		}
	}
}

//#submit_comment, // added our button classes in comments.php
//input.wpcf7-form-control.wpcf7-submit, // added our button classes in contact-form-7-confing.php
.post-password-form input[type='submit'] {
	@include mkdfButtonDefaultStyle();
	@include mkdfButtonSize();
	@include mkdfButtonSolidColor();
	
	cursor: pointer;
	
	&:hover {
		@include mkdfButtonSolidHoverColor();
	}
}

p.form-submit,
.wpcf7-form p {
	margin: 0;
}

#respond textarea,
.mkdf-style-form textarea,
.wpcf7-form textarea {
	display: block;

	.mkdf-ms-explorer & {
		overflow: auto;
	}
}

.mkdf-two-columns-form-without-space {
	.wpcf7-form .ajax-loader {
		display: none;
	}
}

.wpcf7-form {
	.mkdf-cf-custom-style {
		position: relative;
		
		p {
			padding-bottom: 0;
		}

		.wpcf7-form-control {
			&.wpcf7-text {
				padding: 12px 37px 12px 19px;
			}
		}

		.mkdf-cf-custom-style-btn {
			
			button {
				
				&:after {
					content: '';
					position: absolute;
					right: 0;
					bottom: 2px;
					width: calc(100% + 4px);
					height: 10px;
					z-index: -1;
					background-image: url('../img/simple_button_pattern.png');
            		background-repeat: repeat;
            		opacity: .7;
            		transition: .5s;
				}
				
				&:hover:after {
					background-position: 20px 0;
				}
				
			}
		}
		
		.mkdf-btn-subscribe {
			position: absolute;
			display: inline-block;
			height: 50px;
			top: 0;
			right: 7px;
			vertical-align: middle;
			line-height: 1;
			font-size: 22px;
			font-weight: 100;
			color: $default-text-color;
		}
	}
	
	.wpcf7-validation-errors,
	.wpcf7-acceptance-missing {
		border-color: $default-border-color;
		margin: 10px 0;
		padding: 10px;
	}
	
	.wpcf7-not-valid-tip {
		padding: 15px 0;
	}

	.mkdf-cf-custom-dark-style {

		.wpcf7-form-control {
			&.wpcf7-text,
			&.wpcf7-number,
			&.wpcf7-date,
			&.wpcf7-textarea,
			&.wpcf7-select,
			&.wpcf7-quiz {
				@extend %linear-style-2;
			}
		}
	}
}

/* ==========================================================================
   #Comments styles - end
   ========================================================================== */

/* ==========================================================================
   #Global Owl Slider - begin
   ========================================================================== */
.mkdf-owl-slider {
	visibility: hidden;
	
	.owl-stage-outer {
		z-index: 1;
	}
	
	&.mkdf-slider-is-moving {
		.owl-item {
			.mkdf-block-drag-link {
				z-index: -1 !important;
			}
		}
	}
	
	&.mkdf-slider-has-both-nav {
		.owl-nav {
			.owl-prev,
			.owl-next {
				$pos: calc(-50% - 61px); // 61 is the half height of pagination control + top margin
				transform: translateY($pos);
			}
		}
	}
	
	&.mkdf-slider-hide-pagination .owl-dots {
		display: none;
	}
	
	.owl-nav {
		
		.owl-prev,
		.owl-next {
			position: absolute;
			top: 50%;
			width: 55px;
			height: 55px;
			padding: 0;
			margin: 0;
			color: $default-text-color;
			background-color: $additional-background-color;
			border: 0;
			border-radius: 0;
			outline: none;
			-webkit-appearance: none;
			z-index: 3;
			transform: translateY(-50%);
			transition: color .2s ease-out;
			
			&:hover {
				color: $first-main-color;
			}
			
			> span {
				position: relative;
				display: inline-block;
				vertical-align: middle;
				font-size: 38px;
				line-height: 1;
				
				&:before {
					display: block;
					line-height: inherit;
				}
			}
		}
		
		.owl-prev {
			left: 0;
		}
		
		.owl-next {
			right: -1px;
		}
	}

	.mkdf-nav-dark-skin & {

		.owl-nav {

			.owl-prev,
			.owl-next {
				color: $additional-heading-color;
				background-color: $default-heading-color;

				&:hover {
					color: $default-background-color;
				}
			}
		}
	}
	
	.owl-dots {
		position: relative;
		margin: 95px 0 0;
		text-align: center;
		/* Set "counter" to 0 */
		counter-reset: counter;
		transition: all 0.3s ease-in-out;
		
		.owl-dot {
			position: relative;
			display: inline-block;
			vertical-align: middle;
			margin: 0;
			background: none;
			border: 0;
			border-radius: 0;
			outline: none;
			-webkit-appearance: none;
			border-bottom: 1px solid #8b8b8b;
			padding: 4px 18px;
			transition: all 0.3s ease-in-out;

			&:first-child,
			&:last-child {

				&:after {
					content: '';
				    position: absolute;
				    display: block;
					top: 0;
				    width: 21px;
				    height: 100%;
				    border-bottom: 1px solid #8b8b8b;
				}
			}

			&:first-child {

				&:after {
				    left: -21px;
				}
				
				span:before {
					content: '|';
					position: absolute;
					display: block;
					margin: 0;
					top: 7px;
					right: 26px;
					width: 0;
					font-size: 19px;
					transform: rotate(39deg);
					color: #8b8b8b;
				}
			}
			
			&:last-child {

				&:after {
				    right: -21px;
				}
			}
			
			&:before {
				position: relative;
				display: inline-block;
				font-size: 15px;
				font-family: $default-heading-font;
				font-weight: 600;
				top: 0;
				left: 2px;
				color: #8b8b8b;
				/* Increment "counter" by 1 */
				counter-increment: counter;
				content: counter(counter);
				transition: all .3s ease-in-out;
			}
			
			span {
				position: relative;
				
				&:after {
					content: '|';
					position: absolute;
					display: block;
					margin: 0;
					top: 7px;
					left: 17px;
					width: 0;
					font-size: 19px;
					transform: rotate(39deg);
					color: #8b8b8b;
				}
			}
			
			&:hover,
			&.active {
				border-bottom: 1px solid $default-heading-color;
				
				&:before {
					color: $default-heading-color;
				}
			}
		}
	}
	
	.owl-carousel .owl-item img {
		-webkit-transform: translateZ(0); //chrome text cut off fix
	}

	.mkdf-pag-on-slider-horizontal &,
	.mkdf-pag-on-slider-vertical & {

		.owl-nav {

			.owl-prev,
			.owl-next {
				transform: translateY(-50%);
			}
		}

		.owl-dots {
			position: absolute;
			margin: 0;
			z-index: 1;
		}
	}

	.mkdf-pag-on-slider-horizontal & {

		.owl-dots {
			left: 50%;
			bottom: 39px;
			transform: translateX(-50%);
		}
	}

	.mkdf-pag-on-slider-vertical & {

		.owl-dots {
			width: 30px;
			top: 50%;
			right: 10px;
			transform: translateY(-50%);

			.owl-dot {
				display: block;
				border-bottom: none;
				border-left: 1px solid #8b8b8b;
				padding: 12px 9px;

				&:first-child,
				&:last-child {

					&:after {
						top: auto;
						left: -1px;
					    width: 100%;
					    height: 21px;
					    border-bottom: none;
					    border-left: 1px solid #8b8b8b;
					}
				}

				&:first-child {

					&:after {
						top: -21px;
					}

					span:before {
						top: -26px;
						right: auto;
						left: -21px;
						transform: rotate(-39deg);
					}
				}

				&:last-child {

					&:after {
					    bottom: -21px;
					}
				}

				&:before {
					top: 2px;
					left: 0;
					width: 9px;
				}

				span {

					&:after {
						top: 17px;
						left: -21px;
						transform: rotate(-39deg);
					}
				}

				&:hover,
				&.active {
					border-bottom: none;
					border-left: 1px solid $default-heading-color;
				}
			}
		}
	}

	.mkdf-pag-dark-skin & {

		.owl-dots {

			.owl-dot {
				border-bottom: 1px solid $default-background-color;

				&:first-child,
				&:last-child {

					&:after {
					    border-bottom: 1px solid $default-background-color;
					}
				}

				&:first-child {

					span:before {
						color: $default-background-color;
					}
				}

				&:before {
					color: $default-background-color;
				}

				span {

					&:after {
						color: $default-background-color;
					}
				}

				&:hover,
				&.active {
					border-bottom: 1px solid #8b8b8b;

					&:before {
						color: #8b8b8b;
					}
				}
			}
		}
	}

	.mkdf-pag-light-skin & {

		.owl-dots {

			.owl-dot {
				border-bottom: 1px solid $default-heading-color;

				&:first-child,
				&:last-child {

					&:after {
					    border-bottom: 1px solid $default-heading-color;
					}
				}

				&:first-child {

					span:before {
						color: $default-heading-color;
					}
				}

				&:before {
					color: $default-heading-color;
				}

				span {

					&:after {
						color: $default-heading-color;
					}
				}

				&:hover,
				&.active {
					border-bottom: 1px solid #8b8b8b;

					&:before {
						color: #8b8b8b;
					}
				}
			}
		}
	}
	
	+ .mkdf-slider-thumbnail {
		display: inline-block;
		vertical-align: middle;
		list-style: none;
		margin: 20px -10px 0;
		
		> .mkdf-slider-thumbnail-item {
			float: left;
			padding: 0 10px;
			margin-bottom: 20px;
			box-sizing: border-box;
			line-height: 0;
			cursor: pointer;
			
			img {
				border: 2px solid transparent;
				
				transition: border-color 0.2s ease-out;
			}
			
			&.active {
				img {
					border: 2px solid $first-main-color;
				}
			}
		}
		
		$thumbnail_columns_number: ('two', 'three', 'four', 'five', 'six');
		
		@for $i from 0 to length($thumbnail_columns_number) {
			&.mkdf-slider-columns-#{nth($thumbnail_columns_number,$i+1)} {
				
				.mkdf-slider-thumbnail-item {
					width: 100% / ($i+2);
					
					@include ipad-landscape {
						width: 33.3333%;
					}
				}
			}
		}
	}
}

.mkdf-pag-on-slider-vertical {

	&.mkdf-pag-dark-skin {

		.mkdf-owl-slider {

			.owl-dots {

				.owl-dot {
					border-bottom: none;
					border-left: 1px solid $default-background-color;

					&:first-child,
					&:last-child {

						&:after {
							border-bottom: none;
						    border-left: 1px solid $default-background-color;
						}
					}

					&:hover,
					&.active {
						border-bottom: none;
						border-left: 1px solid #8b8b8b;

						&:before {
							color: #8b8b8b;
						}
					}
				}
			}
		}
	}

	&.mkdf-pag-light-skin {

		.mkdf-owl-slider {

			.owl-dots {

				.owl-dot {
					border-bottom: none;
					border-left: 1px solid $default-heading-color;

					&:first-child,
					&:last-child {

						&:after {
							border-bottom: none;
						    border-left: 1px solid $default-heading-color;
						}
					}

					&:hover,
					&.active {
						border-bottom: none;
						border-left: 1px solid #8b8b8b;

						&:before {
							color: #8b8b8b;
						}
					}
				}
			}
		}
	}
}

.mkdf-ms-explorer {

	.mkdf-owl-slider {

		.owl-dots {

			.owl-dot {
				overflow: visible;

				&:first-child {

					span:before {
						top: 9px;
					}
				}

				span {

					&:after {
						top: 9px;
					}
				}
			}
		}
	}

	.mkdf-pag-on-slider-vertical {

		.mkdf-owl-slider {

			.owl-dots {

				.owl-dot {

					&:first-child {

						span:before {
							top: -29px;
						}
					}

					span {

						&:after {
							top: 20px;
						}
					}
				}
			}
		}
	}
}

/* ==========================================================================
   #Global Owl Slider - end
   ========================================================================== */

/* ==========================================================================
   #404 styles - start
   ========================================================================== */

.error404 {
	.mkdf-wrapper {
		padding: 0 !important;
	}
	
	.mkdf-content {
		height: 100vh;
	}
	
	&.admin-bar {
		.mkdf-content {
			height: calc(100vh - 32px);
		}
	}
	
	.mkdf-content-inner {
		@include mkdfTableLayout();
	}
	
	.mkdf-page-not-found {
		@include mkdfTableCellLayout();
		
		padding: 0 37%;
		text-align: center;
		
		.mkdf-404-title {
			margin: 0;
            font-weight: 500;

			@include phone-landscape {
				font-size: 50px;
			}
		}
		
		.mkdf-404-subtitle {
			margin: 35px 0 0;
            font-weight: 500;

			@include phone-landscape {
				font-size: 25px;
			}
		}
		
		.mkdf-404-text {
			margin: 17px 0 0;
			@include mkdfSubTitle();
            color: #e0e0e0;
            font-weight: 400;

		}
		
		.mkdf-btn {
			margin: 54px 0 0;
			
			&.mkdf-btn-light-style {
				color: #585858;
				background-color: #fff;
				border-color: #fff;
				
				&:hover {
					color: #fff !important;
					background-color: transparent !important;
					border-color: #fff !important;
				}
			}
		}
	}
	
	@include ipad-landscape {
		.mkdf-content {
			height: 100%;
			min-height: 100vh;
		}
		
		&.admin-bar {
			.mkdf-content {
				height: 100%;
				min-height: calc(100vh - 32px);
			}
		}
	}

	@include laptop-landscape-large {

		.mkdf-page-not-found {
			padding: 0 33%;
		}
	}

	@include laptop-landscape-mac {

		.mkdf-page-not-found {
			padding: 0 32%;
		}
	}

	@include ipad-landscape {

		&.admin-bar {

			.mkdf-content {
				min-height: calc(100vh - 32px);
			}
		}

		.mkdf-page-not-found {
			padding: 234px 20% 208px;
		}
	}

	@include ipad-portrait {

		.mkdf-page-not-found {
			padding: 368px 100px 330px;
		}
	}

	@include phone-portrait {

		.mkdf-page-not-found {
			padding: 179px 70px 132px;
		}
	}

	@include smaller-phone-portrait {

		.mkdf-page-not-found {
			padding: 226px 20px 0;
		}
	}
}

/* ==========================================================================
   #404 styles - end
   ========================================================================== */

/* ==========================================================================
   #Back to Top styles - start
   ========================================================================== */
#mkdf-back-to-top {
	position: fixed;
	width: 35px;
	right: 25px;
	bottom: 25px;
	margin: 0;
	z-index: 10000;
	opacity: 0;
	visibility: hidden;
	
	&.off {
		opacity: 0;
		visibility: hidden;
		
		transition: opacity 0.15s ease, visibility .0s .15s;
	}
	
	&.on {
		opacity: 1;
		visibility: visible;
		
		transition: opacity 0.3s ease;
	}

	&:hover {

		svg {
			line:nth-of-type(1) {
				animation: mkdf-btt-arrow-animate 1s forwards;
			}

			line:nth-of-type(2) {
				animation: mkdf-btt-arrow-animate-1 1s .3s forwards;
			}

			line:nth-of-type(3) {
				animation: mkdf-btt-arrow-animate-1 1s .3s forwards;
			}
		}
	}

	@keyframes mkdf-btt-arrow-animate {
		0% {
			transform: scaleY(1);
			transform-origin: 0 0;
		}
		33% {
			transform: scaleY(0);
			transform-origin: 0 0;
		}
		40% {
			transform: scaleY(0);
			transform-origin: 0 100%;
		}
		100% {
			transform: scaleY(1);
			transform-origin: 0 100%;
		}
	}

	@keyframes mkdf-btt-arrow-animate-1 {
		0% {
			transform: scale(1);
			transform-origin: 50% 0;
		}
		33% {
			transform: scale(0);
			transform-origin: 50% 0;
		}
		40% {
			transform: scale(0);
			transform-origin: 50% 0;
		}
		100% {
			transform: scale(1);
			transform-origin: 50% 0;
		}
	}

	@keyframes mkdf-btt-arrow-animate-2 {
		0% {
			transform: scaleY(1);
			transform-origin: 50% 0;
		}
		33% {
			transform: scaleY(0);
			transform-origin: 50% 0;
		}
		40% {
			transform: scaleY(0);
			transform-origin: 50% 0;
		}
		100% {
			transform: scaleY(1);
			transform-origin: 50% 0;
		}
	}
	
	> span {
		position: relative;
		display: flex;
		align-items: center;
		justify-content: center;
		width: 35px;
		height: 35px;
		background-color: #212121;
		overflow: hidden;
		
		transition: color 0.2s ease-in-out, background-color 0.2s ease-in-out;
	}
}

/* ==========================================================================
   #Back to Top styles - end
   ========================================================================== */

/* ==========================================================================
   #Self Hosted Video styles - begin
   ========================================================================== */
.mkdf-self-hosted-video-holder {
	.mkdf-video-wrap {
		.mejs-poster,
		.mejs-container,
		.mejs-overlay-play {
			height: 100% !important; /*!important because of the inline style*/
			width: 100% !important; /*!important because of the inline style*/
		}
	}
}

/* ==========================================================================
   #Self Hosted Video styles - end
   ========================================================================== */


.mkdf-animated-layout-holder {

	&.mkdf-item-appear {

		> div:not(.mkdf-lh-content) {
			clip-path: inset(0 0 0 0);
			transform: translateX(0);
			transition: 1.2s cubic-bezier(0.35, 0.71, 0.26, 0.88);
		}

		> .mkdf-lh-content {
			clip-path: inset(0 0 0 0);
			transform: translateX(0);
			transition: 1s .1s cubic-bezier(.91,.13,.26,.88);

			>div, >a {
				opacity: 1;
				transition: .5s .8s;
			}
		}

	}

	> .mkdf-lh-content {
		clip-path: inset(0 100% 0 0);
		transform: translateX(40px);
		transition: 1s;

		>div, >a {
			opacity: 0;	
		}
	}

	> div:not(.mkdf-lh-content) {
		clip-path: inset(0 100% 0 0);
		transform: translateX(20px);
		transition: 1s;
	}
}