/* ==========================================================================
   Select 2 Script styles (Version: 4.0.6 ) - begin
   ========================================================================== */

$select2-input-fields-map: (
		input-fields-height: $sselect-input-height,
		input-fields-side-padding: $input-horizontal-padding,
		input-fields-margin-bottom: $input-margin,
		input-fields-text-size: 14,
		input-fields-text-line-height: 24,
		input-fields-text-color: $default-text-color,
		input-fields-focus-text-color: $default-heading-color,
		input-fields-background-color: $additional-background-color,
		input-fields-focus-background-color: transparent,
		input-fields-border-color: transparent,
		input-fields-focus-border-color: transparent,
		dropdown-background-color: $additional-background-color,
		dropdown-border-color: transparent,
		search-field-text-color: $default-text-color,
		search-field-focus-text-color: $default-heading-color,
		search-field-background-color: #fff,
		search-field-focus-background-color: #fff,
		search-field-border-color: $default-border-color,
		search-field-focus-border-color: $default-heading-color
);

body .select2-container--default { /* just to make sure that this code overwrite styles from others plugin */
	position: relative;
	display: inline-block;
	vertical-align: middle;
	margin: 0 0 #{map-get($select2-input-fields-map, 'input-fields-margin-bottom')}px;
	text-align: left;
	
	* {
		box-sizing: border-box;
	}
	
	&[dir="rtl"] {
		
		.select2-selection--single {
			
			.select2-selection__clear {
				float: left;
			}
			
			.select2-selection__rendered {
				padding: 0;
			}
			
			.select2-selection__arrow {
				left: 0;
				right: auto;
			}
		}
		
		.select2-selection--multiple {
			
			.select2-selection__choice,
			.select2-selection__placeholder,
			.select2-search--inline {
				float: right;
			}
			
			.select2-selection__rendered {
				padding: 0;
			}
			
			.select2-selection__choice {
				margin: 7px auto 0 5px;
			}
			
			.select2-selection__choice__remove {
				margin: 0 auto 0 3px;
			}
		}
	}
	
	&.select2-container--disabled {
		
		.select2-selection--single,
		.select2-selection--multiple {
			background-color: $additional-background-color;
			cursor: default;
			
			.select2-selection__clear {
				display: none;
			}
		}
		
		.select2-selection__choice__remove {
			display: none;
		}
	}
	
	&.select2-container--open {
		
		.select2-selection--single,
		.select2-selection--multiple {
			color: map-get($select2-input-fields-map, 'input-fields-focus-text-color');
		}
		
		.select2-dropdown {
			left: 0;
			
			&.select2-dropdown--above {
				border-bottom-color: transparent;
			}
			
			&.select2-dropdown--below {
				border-top-color: transparent;
			}
		}
	}
	
	.select2-selection--single {
		display: block;
		height: #{map-get($select2-input-fields-map, 'input-fields-height')}px;
		padding: #{(map-get($select2-input-fields-map, 'input-fields-height') - map-get($select2-input-fields-map, 'input-fields-text-line-height')) / 2}px #{map-get($select2-input-fields-map, 'input-fields-side-padding')}px;
		margin: 0;
		font-family: inherit;
		font-size: #{map-get($select2-input-fields-map, 'input-fields-text-size')}px;
		line-height: #{map-get($select2-input-fields-map, 'input-fields-text-line-height')}px;
		font-weight: normal;
		color: map-get($select2-input-fields-map, 'input-fields-text-color');
		background-color: map-get($select2-input-fields-map, 'input-fields-background-color');
		border: 1px solid map-get($select2-input-fields-map, 'input-fields-border-color');
		border-radius: 0;
		outline: none;
		cursor: pointer;
		user-select: none;
		
		.select2-selection__rendered {
			display: block;
			padding: 0;
			line-height: inherit;
			color: inherit;
			overflow: hidden;
			text-overflow: ellipsis;
			white-space: nowrap;
		}
		
		.select2-selection__clear {
			position: relative;
			float: right;
			margin: 0;
			font-weight: inherit;
			cursor: pointer;
		}
		
		.select2-selection__placeholder {
			color: inherit;
		}
		
		.select2-selection__arrow {
			position: absolute;
			top: 0;
			right: 0;
			width: 50px;
			height: 100%;
			display: flex;
			align-items: center;
			justify-content: center;
			font-size: 16px;
			color: $default-heading-color;
			
			b {
				display: none;
			}
			
			&:before {
				display: block;
				line-height: inherit;
				content: '\33';
				font-family: 'ElegantIcons';
				font-style: normal;
			}
		}
	}
	
	.select2-selection--multiple {
		display: block;
		height: #{map-get($select2-input-fields-map, 'input-fields-height')}px;
		padding: #{(map-get($select2-input-fields-map, 'input-fields-height') - map-get($select2-input-fields-map, 'input-fields-text-line-height')) / 2}px #{map-get($select2-input-fields-map, 'input-fields-side-padding')}px;
		margin: 0;
		font-family: inherit;
		font-size: #{map-get($select2-input-fields-map, 'input-fields-text-size')}px;
		line-height: #{map-get($select2-input-fields-map, 'input-fields-text-line-height')}px;
		font-weight: normal;
		color: map-get($select2-input-fields-map, 'input-fields-text-color');
		background-color: map-get($select2-input-fields-map, 'input-fields-background-color');
		border: 1px solid map-get($select2-input-fields-map, 'input-fields-border-color');
		border-radius: 0;
		outline: none;
		cursor: pointer;
		user-select: none;
		
		.select2-selection__rendered {
			list-style: none;
			display: block;
			padding: 0;
			line-height: inherit;
			color: inherit;
			overflow: hidden;
			text-overflow: ellipsis;
			white-space: nowrap;
			
			li {
				margin: 0;
				padding: 0;
			}
			
			.select2-selection__placeholder {
				float: left;
				margin: 0;
				color: inherit;
			}
			
			.select2-selection__clear {
				position: relative;
				float: right;
				margin: 0;
				font-weight: inherit;
				cursor: pointer;
			}
			
			.select2-selection__choice {
				margin: 5px 5px 0 0;
				padding: 0 10px;
				float: left;
				color: #fff;
				background-color: $default-heading-color;
				border: 0;
				border-radius: 0;
				cursor: default;
			}
			
			.select2-selection__choice__remove {
				display: inline-block;
				margin: 0 3px 0 0;
				font-weight: 700;
				color: inherit;
				transition: opacity .2s ease-out;
				cursor: pointer;
				
				&:hover {
					color: inherit;
					opacity: 0.8;
				}
			}
		}
	}
	
	.select2-search--inline {
		float: left;
		
		.select2-search__field {
			width: 100%;
			padding: 0 !important;
			margin: 0 !important;
			font-family: inherit;
			font-size: inherit;
			font-weight: inherit;
			color: inherit;
			border: none !important;
			outline: none;
			-webkit-appearance: none;
			
			&:focus {
				border-color: $default-heading-color;
			}
			
			&::-webkit-search-cancel-button {
				-webkit-appearance: none;
			}
		}
	}
	
	.select2-dropdown {
		position: absolute;
		top: 0;
		left: -100000px;
		display: block;
		width: 100%;
		background-color: map-get($select2-input-fields-map, 'dropdown-background-color');
		border: 1px solid map-get($select2-input-fields-map, 'dropdown-border-color');
		border-radius: 0;
		z-index: 1051;
	}
	
	.select2-search--dropdown {
		display: block;
		padding: 14px 15px 2px;
		
		&.select2-search--hide {
			display: none;
		}
		
		.select2-search__field {
			width: 100%;
			padding: 4px 8px;
			margin: 0;
			font-family: inherit;
			font-size: inherit;
			line-height: 20px;
			font-weight: inherit;
			color: map-get($select2-input-fields-map, 'search-field-text-color');
			background-color: map-get($select2-input-fields-map, 'search-field-background-color');
			border: 1px solid map-get($select2-input-fields-map, 'search-field-border-color');
			outline: none;
			-webkit-appearance: none;
			
			&:focus {
				color: map-get($select2-input-fields-map, 'search-field-focus-text-color');
				background-color: map-get($select2-input-fields-map, 'search-field-focus-background-color');
				border-color: map-get($select2-input-fields-map, 'search-field-focus-border-color');
			}
			
			&::-webkit-search-cancel-button {
				-webkit-appearance: none;
			}
		}
	}
	
	.select2-results {
		display: block;
		
		> .select2-results__options {
			max-height: 200px;
			overflow-y: auto;
		}
	}
	
	.select2-results__options {
		position: relative;
		margin: 5px 0;
		padding: 2px #{map-get($select2-input-fields-map, 'input-fields-side-padding')}px;
		list-style: none;
	}
	
	.select2-results__option {
		padding: 3px 0;
		font-size: #{map-get($select2-input-fields-map, 'input-fields-text-size')}px;
		line-height: #{map-get($select2-input-fields-map, 'input-fields-text-line-height')}px;
		user-select: none;
		transition: color .2s ease-out;
		
		&[aria-selected] {
			cursor: pointer;
		}
		
		&[role=group] {
			padding: 0;
		}
		
		&[aria-disabled=true] {
			color: $default-heading-color;
		}
		
		&[aria-selected=true],
		&[data-selected=true] {
			color: map-get($select2-input-fields-map, 'input-fields-focus-text-color');
			background: none;
		}
		
		.select2-results__option {
			padding-left: 1em;
			
			.select2-results__group {
				padding-left: 0;
			}
			
			.select2-results__option {
				margin-left: -1em;
				padding-left: 2em;
				
				.select2-results__option {
					margin-left: -2em;
					padding-left: 3em;
					
					.select2-results__option {
						margin-left: -3em;
						padding-left: 4em;
						
						.select2-results__option {
							margin-left: -4em;
							padding-left: 5em;
							
							.select2-results__option {
								margin-left: -5em;
								padding-left: 6em;
							}
						}
					}
				}
			}
		}
	}
	
	.select2-results__option--highlighted {
		
		&[aria-selected] {
			color: map-get($select2-input-fields-map, 'input-fields-focus-text-color');
			background: none;
		}
	}
	
	.select2-results__group {
		display: block;
		padding: 7px 0;
		cursor: default;
	}
}

.select2-close-mask {
	position: fixed;
	top: 0;
	left: 0;
	display: block;
	width: auto;
	height: auto;
	min-height: 100%;
	min-width: 100%;
	padding: 0;
	margin: 0;
	background-color: #fff;
	border: 0;
	opacity: 0;
	z-index: 99;
	filter: alpha(opacity=0);
}

.select2-hidden-accessible {
	position: absolute !important;
	width: 1px !important;
	height: 0 !important;
	padding: 0 !important;
	line-height: 1;
	border: 0 !important;
	overflow: hidden !important;
	white-space: nowrap !important;
	clip: rect(0 0 0 0) !important;
	clip-path: inset(50%) !important;
}